import { search } from './index';

export default class {
  constructor(apiKey, maxResult = 15) {
    this.apiKey = apiKey;
    this.maxResult = maxResult;
    this.q = '';
    // this.previousPageToken = '';
    this.nextPageToken = '';
    this.callbackOK;
  }

  requestNext() {
    this.request(this.q, this.nextPageToken);
  }

  request(q, pageToken = '') {
    if (!q || q === '') {
      return;
    }
    this.q = q;
    search(q, pageToken)
      .then(this.onSearchOk.bind(this))
      .catch(this.onSearchError.bind(this));
  }

  onSearchOk(result) {
    this.nextPageToken = result.nextPageToken;
    console.log('this.nextPageToken', this.nextPageToken);
    this.callbackOK({ ...result, q: this.q });
  }

  onSearchError(error) {
    // TODO implement
  }
}

